.Cake {
  width: 525px;
  height: 525px;
  -ms-flex-order: -1;
      order: -1;
}

.Cake-img {
  -webkit-transition: 0.3s ease;
  -o-transition: 0.3s ease;
  transition: 0.3s ease;
  cursor: url(cursor.png), pointer;
}

.Cake-img:hover {
  -webkit-transform: scale(1.025, 1.025);
      -ms-transform: scale(1.025, 1.025);
          transform: scale(1.025, 1.025);
}



